@import "../../css/shared.scss";

.p-referral-code{
  padding: px(210) px(160) px(120) px(160);
  text-align: center;

  p{
    font-size: px(52);
    margin-top: px(100);
    word-break: break-all;
  }

  .c-btn{
    display: block;
    width: px(670);
    font-size: px(35);
    border-radius: px(45);
    margin: px(120) auto 0 auto;
  }

  .qrcode{
    img{
      width: px(600);
      height: px(600);
      margin: 0 auto;
      border: px(20) solid #fff;
      background: #fff;
    }
  }

}
