$px: 0.01rem;
@function px($n) {
    @return $n * $px;
}

$space: px(30);
@mixin shadow {
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
}

@mixin ellipsis {
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@mixin toolsButton {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-size: px(30);
    font-weight: bold;
    background: linear-gradient( 180deg, rgba(203, 45, 62, 1), rgba(239, 71, 58, 1));
    border-radius: px(10);
    vertical-align: middle;
    color: #fff;
    border: none;
    outline: 0;
    line-height: px(80);
}

@mixin blockFormWrapper {
    width: px(690);
    background: rgba(255, 255, 255, 1);
    box-shadow: 0 0 px(10) 0 rgba(0, 0, 0, 0.2);
    border-radius: px(16);
    margin: px(32) auto;
    box-sizing: border-box;
}

//$bgUrl: "\./images/";
$color-primary: #fff;
$color-sub: #b2b2b2;
$color-bg: #00060a;
$linear-gradient: linear-gradient(to right, #6f3ac3, #4d2b8e);
